export default {
	data() {
		return {
			userDetail:''
			
		};
	},
	onShow:function(){
		this.fetch()
	},
	onLoad:function(){
	    this.fetch()	
	},
	methods:{
		showPicture:function(src){
			uni.previewImage({
				urls: [src],
				success:function(){
					console.log('正在预览中')
				}
				
			})
		},
		async fetch(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
		},
		toEdit:function(){
			uni.navigateTo({
				url: './editPayAddress',
			});
		}
	}
}
